.delivery-info {
	text-align: center;
	padding: 53px 0 32px;
	color: $brown;
	@include md {
		padding: 10px 0 36px; }
	&__inner {
		max-width: 1200px; }
	&__title.title {
		color: $green; }
	&__wrap {
		margin: 15px -45px 0;
		display: flex;
		justify-content: center;
		@include lg {
			margin: 25px -25px 0; }
		@include md {
			flex-wrap: wrap;
			margin: 25px 0 0; } }
	&__item {
		padding: 0 45px;
		// max-width: 400px
		width: 33.3%;
		@include lg {
			padding: 0 25px; }
		@include md {
			width: 100%;
			max-width: none;
			padding: 0;
			&:not(:first-child) {
				margin-top: 20px; } } }
	&__btn {
		text-align: center;
		margin-top: 30px;
		a {
			min-width: 170px; }
		@include md {
			.btn {
				background-color: $pink; } } } }


.delivery-item {
	text-align: center;
	width: 100%;
	&__img {
		min-height: 194px;
		display: flex;
		justify-content: center;
		align-items: flex-end;
		@include md {
			min-height: auto; }
		img {
			display: block;
			max-width: 100%; } }
	&__text {
		margin-top: 27px;
		@include f(27px, 700);
		@include circe;
		line-height: 1.2;
		@include lg {
			font-size: 20px; }
		@include ms {
			font-size: 16px; }
		span {
			text-transform: uppercase;
			display: block; }
		@include md {
			margin-left: 30px;
			margin-top: 0;
			max-width: 300px; }
		@include ms {
			max-width: 250px; } }
	@include md {
		display: flex;
		justify-content: center;
		align-items: center; } }
