.menu {
	overflow: hidden;
	&__inner {
		position: relative;
		@include sm {
			padding: 0; } }
	&__prev-arrow,
	&__next-arrow {
		position: absolute;
		z-index: 2;
		cursor: pointer;
		user-select: none;
		top: 72px;
		border-radius: 50%;
		transition: background-color .3s;
		@include hover {
			background-color: rgba($black, .2); }
		@include lg {
			top: 40px; }
		.icon {
			display: block;
			font-size: 57px;
			@include lg {
				font-size: 40px; }
			@include xs {
				font-size: 30px; } }
		.dishes-menu & {
			top: 0px;
			color: $brown; }
		.dishful & {
			top: 44px; } }
	&__prev-arrow {
		left: 71px;
		@include sm {
			left: 34px; }
		@include xs {
			left: 10px; }
		.dishes-menu & {
			left: 75px; } }
	&__next-arrow {
		right: 542px;
		@include xxl {
			right: 470px; }
		@include lg {
			right: 400px; }
		@include md {
			right: 71px; }
		@include sm {
			right: 34px; }
		@include xs {
			right: 10px; }
		.dishes-menu & {
			right: 75px; } }
	.slick-list {
		overflow: visible; } }

.menu-slider {
	white-space: nowrap;
	// overflow: hidden
	opacity: 0;
	visibility: hidden;
	transition: opacity .3s, visibility .3s;
	&.slick-initialized {
		white-space: normal;
		// overflow: visible
		opacity: 1;
		visibility: visible; }
	.dishful & {
		white-space: normal;
		// overflow: visible
		opacity: 1;
		visibility: visible; }
	&:before,
	&:after {
		content: '';
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($white, .5);
		z-index: 1;
		@include sm {
			display: none; } }
	&:before {
		left: 100%; }
	&:after {
		right: 100%; }
	&__slide {
		height: 850px;
		@include lg {
			height: 700px; }
		@include sm {
			height: 580px; }
		&-wrap {
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			opacity: 1;
			visibility: visible;
			transition: opacity .3s, visibility .3s; } }
	&__top {
		padding: 51px 43px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		background-color: rgba($white, .5);
		line-height: 1.1;
		color: $brown;
		@include lg {
			padding: 30px; }
		@include sm {
			background-color: $powder;
 }			// display: none // !!!!!!!!!!!!!!!!!!!!!!!!!!
		&.menu-slider__top_display {
			display: none;
			@include sm {
				display: block; } }
		.dishful & {
			padding-top: 41px;
			padding-bottom: 60px; } }
	&__bottom {
		@include sm {
 }			// display: none //!!!!!!!!!!!!!!!!!!!!!!
		&.menu-slider__bottom_display {
			display: none;
			@include sm {
				display: block; } } }
	&__text {
		padding: 0 170px 0 73px;
		max-width: 1150px;
		width: 100%;
		@include xxl {
			padding-right: 100px; }
		@include lg {
			padding-left: 50px;
			padding-right: 120px; }
		@include sm {
			padding: 0 80px;
			text-align: center; }
		@include xs {
			padding: 0 30px; } }
	&__title {
		font-size: 45px;
		font-weight: 700;
		@include circe;
		@include xxl {
			font-size: 37px; }
		@include lg {
			font-size: 29px; }
		span {
			font-weight: 900; } }
	&__subtitle {
		margin-top: 8px;
		font-weight: 500;
		font-size: 24px;
		@include xxl {
			font-size: 20px; }
		@include lg {
			font-size: 16px; } }
	&__btn {
		flex-shrink: 0;
		@include md {
			display: none; }
		&_mob {
			display: none;
			@include md {
				display: block;
				text-align: center;
				padding-top: 35px; }
			@include sm {
				padding-top: 210px; }
			@include ms {
				padding-top: 190px; } } }
	.slick-dots {
		display: flex;
		position: absolute;
		bottom: 120px;
		left: 50%;
		transform: translateX(-50%);
		li {
			padding: 0 10px; }
		@include ms {
			bottom: 95px; }
		.hero__dot {
			&:before {
				content: '';
				position: absolute;
				@include vcenter;
				width: 200%;
				height: 200%; } } } }

.menu-list {
	display: flex;
	background-color: rgba($black, .35);
	@include sm {
		flex-wrap: wrap;
		position: relative;
		top: 180px; }
	@include ms {
		top: 140px; }
	&__item {
		width: 33.33%;
		position: relative;
		color: $white;
		// +hover
		/// .menu-ingredients
		/// 	opacity: 1
		/// 	visibility: visible
		/// .menu-list__ingredients-wrap
		/// 	opacity: 1
		/// 	visibility: visible
		@include sm {
			width: 100%;
			position: static; }
		&_green {
			.menu-list__link {
				// +hover
				//ackground-color: rgba($green, .5)
				&.is-active {
					background-color: rgba($green, .5); } } }
		&_orange {
			.menu-list__link {
				// +hover
				//ackground-color: rgba($orange, .5)
				&.is-active {
					background-color: rgba($orange, .5); } } }
		&_pink {
			.menu-list__link {
				// +hover
				//ackground-color: rgba($pink, .5)
				&.is-active {
					background-color: rgba($pink, .5); } } } }
	&__link {
		padding: 45px 40px 44px;
		display: flex;
		justify-content: center;
		align-items: center;
		text-transform: uppercase;
		font-size: 30px;
		font-weight: 700;
		@include circe;
		transition: background-color .3s;
		// +hover
		// 	.icon
		// 		background-color: rgba($black, .2)
		&.is-active {
			.icon {
				background-color: rgba($black, .2); } }
		@include xxl {
			font-size: 25px; }
		@include lg {
			font-size: 18px;
			padding: 25px; }
		@include md {
			padding: 15px;
			font-size: 16px; }
		@include sm {
			font-size: 28px;
			position: relative;
			padding: 25px;
			padding-left: 40px; }
		@include ms {
			font-size: 20px;
			padding: 15px; }
		span {
			position: relative;
			display: block;
			padding-right: 86px;
			@include lg {
				padding-right: 70px; }
			@include md {
				padding-right: 40px; }
			@include sm {
				// padding-right: 70px
				display: flex;
				align-items: center;
				padding-right: 0;
				justify-content: space-between;
				width: 100%; }
			.icon {
				content: '';
				position: absolute;
				font-size: 55px;
				top: 50%;
				transform: translateY(-50%);
				right: 0;
				border-radius: 50%;
				@include lg {
					font-size: 40px; }
				@include md {
					font-size: 25px; }
				@include sm {
					font-size: 40px;
					position: static;
					transform: none; } } } } }

.menu-ingredients {
	padding: 1.7em 1.7em 1.7em 3em;
	overflow: auto;
	color: $brown;
	font-size: 32px;
	font-weight: 700;
	@include circe;
	text-transform: uppercase;
	line-height: 1.1;
	scrollbar-color: #c7c7cc #efeff4;
	scrollbar-width: thin;
	@include xxl {
		font-size: 26px;
		max-height: 450px; }
	@include lg {
		font-size: 20px; }
	@include md {
		padding: 2em 1.5em; }
	@include sm {
		padding: 1.3em 2.2em 3.5em; }
	&::-webkit-scrollbar {
		width: 6px;
		height: 6px;
		@include sm {
			display: none; } }
	&::-webkit-scrollbar-track {
		background: #efeff4;
		@include sm {
			display: none; } }
	&::-webkit-scrollbar-thumb {
		background: #c7c7cc;
		@include sm {
			display: none; } }
	&::-webkit-scrollbar-thumb:hover {
		background: #b1b1b2;
		@include sm {
			display: none; } }
	&__item {
		transition: color .3s;
		&:not(:first-child) {
			margin-top: 20px; }
		.menu-list__item_green & {
			@include hover {
				color: $green; } }
		.menu-list__item_pink & {
			@include hover {
				color: $pink; } }
		.menu-list__item_orange & {
			@include hover {
				color: $orange; } } }
	// &.is-active
	// 	+md
	// 		opacity: 1
 }	// 		visibility: visible

.menu-list__ingredients-wrap {
	overflow: auto;
	position: absolute;
	opacity: 0;
	visibility: hidden;
	transition: opacity .3s, visibility .3s;
	bottom: 100%;
	width: 100%;
	left: 0;
	// padding: 46px 45px 41px 76px
	background-color: rgba($white, .5);
	max-height: 500px;
	// position: relative
	// opacity: 0
	// visibility: hidden
	// transition: opacity .2s, visibility .2s
	// z-index: -5
	// transform: translateX(-100%)
	&.is-active {
		opacity: 1;
		visibility: visible;
		z-index: 2; }
	.slick-list {
		overflow: hidden; }
	.slick-dots {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 30px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		li {
			line-height: 0;
			&:not(:first-child) {
				margin-left: 18px; } } } }
