.dishes-menu {
	padding: 27px 0 30px;
	margin-bottom: 30px;
	&.bg_orange {
		background-color: $orangeSlide; }
	&.bg_pink {
		background-color: $pinkSlide; }
	// &.bg_green
	// 	background-color: $greenLight
	&__inner {
		position: relative; }
	&__title {
		text-align: center; }
	&__slider-wrap {
		display: flex;
		align-items: center; }
	&__btn {
		margin-left: 80px;
		// text-align: center
		// position: absolute
		// top: 190px
		// left: 50%
 } }		// transform: translateX(-50%)

.dishes-slider {
	max-width: 77%;
	padding-top: 30px;
	white-space: nowrap;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
	transition: opacity .3s, visibility .3s;
	margin: 0 -20px;
	&.slick-initialized {
		white-space: normal;
		opacity: 1;
		visibility: visible; }
	&__slide {
		max-width: 420px;
		width: 100%;
		position: relative;
		padding: 0 20px; }
	&__plus {
		position: absolute;
		margin-top: 53%;
		right: 50px;
		top: 0;
		z-index: 1;
		@include lg {
			margin-top: 45%;
			right: 30px; } } }
.dish-item {
	display: block;
	background-color: $white;
	position: relative;
	@include hover {
		&:before {
			opacity: 1; } }
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba($black, .2);
		z-index: 1;
		opacity: 0;
		transition: opacity .3s; }
	&__img {
		@include cover;
		height: 0;
		padding-bottom: 65%; }
	&__title {
		&.title {
			font-weight: 400;
			color: $black;
			line-height: 1.2; } }
	&__text {
		padding: 50px 30px 30px;
		// position: relative
		@include lg {
			padding: 40px 20px 30px; } }
	&__bottom {
		padding-top: 30px;
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__weight {}
	&__price {
		&.title {
			text-transform: none;
			line-height: 1; } } }
